$cell-size: 100px;
$cell-indent: 1px;
$padding: 10px;
$cell-bgc: #eee;
$cell-bgc-hover: rgb(242, 213, 218);
$cell-border: solid 1px #000;
$font-family: arial, helvetica, sans-serif;
$days-in-week: 7;
$days-in-month: 31;
$month-length: 28;
$week:
  "mon" 0,
  "tue" 1,
  "wed" 2,
  "thu" 3,
  "fri" 4,
  "sat" 5,
  "sun" 6;
